.brk-elev {
  position: absolute;
  width: 19px;
  height: 19px;
  z-index: 10;
}

.brk-elev:before, .brk-elev:after {
  content: '';
  position: absolute;
  background-color: #000;
}

.brk-elev:before {
  width: 100%;
  height: 1px;
  top: 9px;
  left: 0;
}

.brk-elev:after {
  width: 1px;
  height: 100%;
  top: 0;
  left: 9px;
}

.brk-elev-1 {
  top: -9px;
  left: -9px;
}

.brk-elev-2 {
  top: -9px;
  right: -10px;
}

.brk-elev-3 {
  bottom: -10px;
  right: -10px;
}

.brk-elev-4 {
  bottom: -10px;
  left: -9px;
}
